@tailwind base;
@tailwind components;
@tailwind utilities;

/* Fade-in animation */
@keyframes fadeIn {
    0% { opacity: 0; transform: translateY(20px); }
    100% { opacity: 1; transform: translateY(0); }
  }
  
  .fade-in {
    animation: fadeIn 1s ease-out forwards;
  }
  
  .fade-in-slow {
    animation: fadeIn 1.5s ease-out forwards;
  }
  